.search-by-string {
  position: relative;
  height: 45px;
  font-family: "Roboto";
  box-sizing: border-box;
}

.search-by-string__container {
  position: relative;
  display: flex;
  height: 100%;
  box-sizing: border-box;
  border-radius: 8px;
  padding-left: 15px;
  padding-right: 18px;
  justify-content: space-between;
  border-width: 1px;
  border-style: solid;
}
.search-by-string__container_theme-day {
  color: #000;
  border: 1px solid rgba(0, 0, 0, 0.3);
}
.search-by-string__container_theme-day:focus-within {
  border: 1px solid #000;
}
.search-by-string__container_theme-naight {
  color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.3);
}
.search-by-string__container_theme-naight:focus-within {
  border: 1px solid #fff;
}

.search-by-string__notice-not-found {
  position: absolute;
  left: 15px;
  bottom: 0;
  font-size: 13px;
  line-height: 15px;
  font-weight: 400;
  font-style: italic;
}

.search-by-string__input {
  border: none;
  outline: none;
  width: 100%;
  align-self: center;
  font-weight: 400;
  line-height: 16px;
  font-family: "Roboto";
  box-sizing: border-box;
  background-color: inherit;
  color: inherit;
  border-radius: inherit;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.search-by-string__input_theme-day::-moz-selection {
  background-color: #000;
  color: #fff;
}
.search-by-string__input_theme-day::selection {
  background-color: #000;
  color: #fff;
}
.search-by-string__input_theme-naight::-moz-selection {
  background-color: #fff;
  color: #000;
}
.search-by-string__input_theme-naight::selection {
  background-color: #fff;
  color: #000;
}

.search-by-string__error-only-letter {
  position: absolute;
  left: 14px;
  bottom: 0;
  font-size: 13px;
  line-height: 15px;
  font-weight: 400;
  color: red;
}